*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body,
p,
ol,
ul,
li,
dl,
dt,
dd,
blockquote,
figure,
fieldset,
legend,
textarea,
pre,
iframe,
hr,
h1,
h2,
h3,
h4,
h5,
h6 {
  padding: 0;
  margin-top: 0;
}

html:last-child,
body:last-child,
p:last-child,
ol:last-child,
ul:last-child,
li:last-child,
dl:last-child,
dt:last-child,
dd:last-child,
blockquote:last-child,
figure:last-child,
fieldset:last-child,
legend:last-child,
textarea:last-child,
pre:last-child,
iframe:last-child,
hr:last-child,
h1:last-child,
h2:last-child,
h3:last-child,
h4:last-child,
h5:last-child,
h6:last-child {
  margin-bottom: 0;
}

ul {
  list-style: none;
}

button,
input,
select,
textarea {
  margin: 0;
}

img,
embed,
iframe,
object,
audio,
video {
  max-width: 100%;
  height: auto;
}

iframe {
  border: 0;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
}

td,
th {
  padding: 0;
  text-align: left;
}

html {
  --pfe-broadcasted--text: var(--pfe-theme--color--text, #333);
  --pfe-broadcasted--link: var(--pfe-theme--color--link, #06c);
  --pfe-broadcasted--link--hover: var(--pfe-theme--color--link--hover, #003366);
  --pfe-broadcasted--link--focus: var(--pfe-theme--color--link--focus, #003366);
  --pfe-broadcasted--link--visited: var(--pfe-theme--color--link--visited, rebeccapurple);
  --pfe-broadcasted--link-decoration: none;
  --pfe-broadcasted--link-decoration--hover: underline;
  --pfe-broadcasted--link-decoration--focus: underline;
  --pfe-broadcasted--link-decoration--visited: none;
  font-family: sans-serif;
  line-height: var(--pfe-theme--line-height, 1.5);
}

body {
  font-family: var(--pfe-theme--font-family, "Overpass", Overpass, Helvetica, helvetica, arial, sans-serif);
  font-size: var(--pfe-theme--font-size, 16px);
  font-weight: var(--pfe-theme--font-weight--normal, 500);
  line-height: var(--pfe-theme--line-height, 1.5);
  color: var(--pfe-theme--color--text, #333);
  text-align: left;
  margin: 0;
}

a {
  color: var(--pfe-broadcasted--link, #06c);
  text-decoration: var(--pfe-broadcasted--link-decoration, none);
}

a:hover {
  color: var(--pfe-broadcasted--link--hover, #003366);
  text-decoration: var(--pfe-broadcasted--link-decoration--hover, underline);
}

a:focus {
  color: var(--pfe-broadcasted--link--focus, #003366);
  text-decoration: var(--pfe-broadcasted--link-decoration--focus, underline);
}

a:visited {
  color: var(--pfe-broadcasted--link--visited, rebeccapurple);
  text-decoration: var(--pfe-broadcasted--link-decoration--visited, none);
}

button,
a {
  cursor: pointer;
}
